import { fetchCsrfToken, loadWAFData } from "./helpers";

export const fetchChangeActionFlow = async (objectId) => {
  try {
    const headers = await fetchCsrfToken();
    if (!headers) throw new Error("Missing CSRF headers");

    const caUrl = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/changeaction/${objectId}?$fields=flowDown`;

    const WAFData = await loadWAFData();

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(caUrl, {
        method: "GET",

        headers: {
          ...headers,
          "Content-Type": "application/json",
        },
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    return response;
  } catch (error) {
    console.error("[Change Action] Failed to fetch flowDown:", error);
    throw error;
  }
};
