import React, { useEffect, useRef } from "react";
import "./DragAndDrop.css"; // Import styles for the component

const DragAndDropComponent = ({ onDropSuccess }) => {
  const dropRef = useRef(null);

  useEffect(() => {
    const handleDragOver = (event) => {
      event.preventDefault();
      event.dataTransfer.dropEffect = "copy";
    };

    const handleDrop = (event) => {
      event.preventDefault();

      const data = event.dataTransfer.getData("text/plain");

      try {
        const parsed = JSON.parse(data);
        console.log("Dropped object:", parsed);

        if (onDropSuccess) {
          onDropSuccess(parsed); // Let parent handle further logic
        }
      } catch (error) {
        console.warn("Invalid drop data", error);
      }
    };

    const dropArea = dropRef.current;

    if (dropArea) {
      dropArea.addEventListener("dragover", handleDragOver);
      dropArea.addEventListener("drop", handleDrop);
    }

    return () => {
      if (dropArea) {
        dropArea.removeEventListener("dragover", handleDragOver);
        dropArea.removeEventListener("drop", handleDrop);
      }
    };
  }, [onDropSuccess]);

  return (
    <div ref={dropRef} className="droppable-container mt-4">
      <img
        style={{ width: "90px", height: "90px" }}
        src="https://thewhitechamaleon.github.io/testrapp/images/drag.png"
        alt="Data Collect"
        className="search-icon"
      />
      <span className="drag-and-drop-text">Drag and Drop your object here</span>
    </div>
  );
};

export default DragAndDropComponent;
